<template>
    <a-layout class="main_page">
        <a-layout-header style="background-color: #00BFFF"> <h1>{{ msg }}</h1> </a-layout-header>
        <a-layout-content>            
            <a-row justify="center">
                <a-col :span="18" >

                <div class="controlPanel" style="display: flex; justify-content: center;">
                    <a-card title="Parameters Setting For Deep Learning in Databases" :bordered="false" class="cardContainer">
                        <!-- Gudiance on using gui -->
                        <template v-if="showTutorial">
                            <a-steps v-model:current="currentStep">
                                <a-step title="Step 1" description="Introduction" /> 
                                <a-step title="Step 2" description="Preparation" />
                                <a-step title="Step 3" description="Retreiving result" /> </a-steps>

                            <div class="stepContent">

                                <a-descriptions title="" v-if="currentStep === 0">
                                    <a-descriptions-item>
                                        <span class="Option_data_title">
                                            This project aims to assist researchers to select queries, choose datasets with availiable models to explore query optimization results.
                                            <br /> You may follow the following steps to investigate performance of NNGP model in optimizing query planning and execution.
                                            <br /> If you know how to use this gui, you may click the <a-button style="margin-right: 3px; margin-left: 3px" @click="viewTutorial">
                                                {{ showTutorial ? "Close" : "Guidance" }}</a-button> to close this guidance.
                                        </span>
                                    </a-descriptions-item>
                                </a-descriptions>

                                <a-descriptions v-else-if="currentStep === 1">
                                    <a-descriptions-item >
                                        Before actually runnning the nngp model and compare with the PostgreSQL Planner,  <br>
                                        First, you might generate sets of training and testing queries for exploration. <br>
                                        If you do not wish to generate query, you can still use the built in query sets. <br>
                                        <pre>
                                        You need to choose bewteen the given Dataset: 
                                        min-max numbers of joining: 
                                        Equi-join and Non-equi-join ratio:
                                        Train and Test query ratio: 
                                        After selecting all required parameters, server will generate the query for nngp model.
                                        You can now move to next steps
                                        </pre>
                                    </a-descriptions-item>
                                </a-descriptions>

                                <a-descriptions v-else-if="currentStep === 2">
                                    <a-descriptions-item >
                                        After 
                                    </a-descriptions-item>
                                </a-descriptions>
                            </div>

                        </template>

                    <div>
                        <a-divider style="background-color: #00BFFF" ></a-divider>

                        <!-- possible Tutorial  adjust the position 
                        !!! -->
                        <template v-if="!isTrain">
                            <div class="selection">
                                <span class="Option_data_title">Choose a Model: </span>
                                <a-cascader v-model="selectModel" @change="changeModel" :options="modelStrings" placeholder="Select a model.">
                                </a-cascader>
                            </div>
                            <div class="selection">
                                <span class="Option_data_title">Choose a dataset: </span>
                                <a-cascader v-model="selectDataSet" @change="changeDataSet" :options="dataStrings" placeholder="Select a dataset." >
                            </a-cascader>                    
                            </div>
                            <div class="selection">
                                <span class="Option_query_title">Choose the number of table(s) to be joined: </span>
                                <a-cascader v-model="selectQuery" @change="changeQuery" :options="queryStrings" placeholder="Select query set."></a-cascader>                    
                            </div>
                            <div class="selection">
                                <span class="Option_query_num_title">Choose the number of queries: </span>
                                <a-cascader v-model="selectQueryNum" @change="changeQueryNum" :options="querynumStrings" placeholder="Select query number."
                                ></a-cascader>                 
                            </div>
                            <div class="slideControl">
                                <span>Move the slider for train_test ratio: [train: {{this.test_ratio}}, non-join: {{ 100 - this.test_ratio}}]</span>
                                <br />
                                <a-slider v-model:value="train_ratio" class="train_test_ration_title"
                                :marks="marksSlider" :tooltip-visible="true" :step="5" />
                            </div>
                            <div class="slideControl">
                                <span>Move the slider for Join_Non-Join query ratio: [join: {{this.join_ratio}}, non-join: {{ 100 - this.join_ratio}}]</span>
                                <br />
                                <a-slider v-model:value="join_ratio" class="train_test_ration_title"
                                :marks="marksSlider" :tooltip-visible="true" :step="5" />
                            </div>

                            <div class="buttonContainer">
                                <a-button type='primary' @click="confirm" >Run Model </a-button>
                                <a-button style="margin-right: 6px; margin-left: 6px" @click="viewTutorial">
                                    {{ showTutorial ? "Close" : "Guidance" }}
                                </a-button> 
                                <a-button style="margin-left: 6px" danger ghost @click="clean">Clean Data</a-button> 
                                <a-button style="margin-left: 6px" type='dashed' @click="switchMode">Training</a-button>
                                <a-button type='primary' @click="ratioSelection">ratio</a-button>  
                            </div>

                        </template> 

                        <template v-else>
                                <div>
                                <a-progress type="circle" :percent="75" :format="percent => `${percent} Days`" />
                                <a-progress type="circle" :percent="100" :format="() => 'Done'" />
                                </div>
                                <a-button type='primary' @click="switchMode">Train Model</a-button>
                        </template> 
                    </div>

                    </a-card>
                <!-- end of first div tag-->
                </div>

                <a-divider style="background-color: #00BFFF" ></a-divider>
                <!-- Confirm dialogue for running model -->
                <a-modal v-model:visible="modelVisible" title="Model Running" 
                ok-text="confirm" cancel-text="cancel" @ok="runModel">
                <p>
                    You are running model <span class="highlightText">{{selectModel}}</span>, 
                    with dataset <span class="highlightText">{{selectDataSet}}</span>,
                    joining <span class="highlightText">{{selectQuery}}</span> tables 
                    with <span class="highlightText">{{selectQueryNum}}</span> query.
                </p>
                </a-modal>

                <!-- show schema -->
                <template v-if="selectDataSet == 'IMDB'">
                    <div class="schemeDisplay">
                        <span>Dataset: {{ this.selectDataSet }}</span>
                        <span>{{ checked ? "Hide" : "Show" }} Schema <a-switch v-model:checked="checked" style="margin-left: 12px;"/></span>
                    </div>
                    <a-table v-if="checked" :columns="tableSchema"  :dataSource="ImdbSchema" :pagination=false ></a-table> <br/>
                    <a-image v-if="checked" :width="750" src="http://137.189.59.166:8000/imdb.png" />
                </template>
                <template v-else-if="selectDataSet === 'TPCDS'">
                    <div class="schemeDisplay"> 
                        <span>  Dataset: {{ this.selectDataSet }}</span>
                        <span>{{ checked ? "Hide" : "Show" }} Schema <a-switch v-model:checked="checked" style="margin-left: 12px;"/></span>
                    </div>
                    <a-table v-if="checked" :columns="tableSchema" :dataSource="TpcdSchema" :pagination=false ></a-table> <br/>
                    <a-image v-if="checked" :width="750" src="http://137.189.59.166:8000/tpcds.png" />
                </template>
                <template v-else><span>Schema will be shown when you select the dataset</span></template>

                <a-divider style="background-color: #00BFFF" ></a-divider>

                <!-- response data as a table -->
                <template v-if="isShow">

                    <p>
                    Result of running model <span class="highlightText">{{selectModel}}</span>, 
                    with dataset <span class="highlightText">{{selectDataSet}}</span>,
                    joining <span class="highlightText">{{selectQuery}}</span> tables 
                    with <span class="highlightText">{{selectQueryNum}}</span> query.
                    <a-button @click="generateImage();"> image</a-button>
                    </p>

                    <template v-if="isImageShow">
                    <a-image :height="250" :width="500"
                        :src=avgImage />
                    <a-image :height="250" :width="500"
                        :src=planImage />
                    <a-image :height="250" :width="500"
                        :src=execImage />
                    <br>
                    <span>Here is lowest 10 and highest 10 performance bewteen PostgreSQL Planner and NNGP model</span>
                    <a-image :height="250" :width="350"
                        :src="var_exec_low" />
                    <a-image :height="250" :width="350"
                        :src="var_exec_high" />
                    <a-image :height="250" :width="350"
                        :src="var_plan_low" />
                    <a-image :height="250" :width="350"
                        :src="var_plan_high" />
                    </template>

                    <!-- display table -->
                    <a-table :columns="reponseDataColumns" :dataSource="displayData" @change="headerChange" >
                        <template #bodyCell="{ column }">
                            <template v-if="column.key === 'operation'"><a>action</a></template>
                        </template>
                    </a-table>

                </template>

                </a-col>
             </a-row>
             <!-- back into top place -->
            <a-back-top />
        </a-layout-content>

                    <a-layout-footer>
                        @seem4999
                    </a-layout-footer>

    </a-layout>
</template>


<script>
import { modelStrings, dataStrings, queryStrings, querynumStrings, tableSchema, ImdbSchema, TpcdSchema, reponseDataColumns, ForestSchema } from '@/data/optionstr';
import { GUIService } from '@/services/services';
import { http } from '@/services/http_common';
import axios from "axios";
import { ref } from 'vue';



export default {
    name: "SelectComponent",
    setup() {
        const marksSlider = ref({
            0: '0 %',
            25: '25 %',
            50: '50 %',
            75: '75 %',
            100: '100 %'  
        });
        return {
            marksSlider
        }
    },
    data() {
        return {
            click_msg: "SEEM FYP Project", // title 
            // parameter options 
            modelStrings, dataStrings, queryStrings, querynumStrings,
            // Schema options
            tableSchema, ImdbSchema, TpcdSchema, ForestSchema,
            // schemea: "Schema will be shown here", 
            // selection control:
            selectModel: 'NNGP', selectDataSet: '',
            selectQuery: '', selectQueryNum: '',
            // Gui Control
            displayData: "", // json data
            reponseDataColumns, // table to be shown
            isShow: false, // to display result
            modelVisible: false, // show confirm dialogue
            checked: true, // showing schmea or not 

            // reponse image source
            isImageShow: false,
            var_avgImg:"",
            var_planImg:"", var_plan_low:"", var_plan_high:"",
            var_execImg:"", var_exec_low:"", var_exec_high:"",
            // guidance 
            showTutorial: true,
            currentStep: 0,

            // train model 
            isTrain: false,
            train_ratio: 80,
            join_ratio: 50,
            min_join:"",
            max_join:"",
            generate_query: false,
        }
    },
    components: {
    },
    computed: {
        msg() { return this.click_msg },
        imgSource4() {
            return this.var_planImg;
        },
        avgImage() {
            console.log("average image link changed")
            return this.var_avgImg;
        },
        planImage() {
            return this.var_planImg;
        },
        execImage() {
            return this.var_execImg;
        }
    },
    methods: {
        run4() {
            GUIService.get4().then(response => { 
                // this.imgSource4 = "data:image/png;base64," + response.data;
                // this.var_imgSource4 = "";
                // this.var_imgSource4 = response.data;
                // console.log("From VUE: Response = " + this.var_imgSource4);
                // console.log("From VUE: url = " + this.imgSource4);
                console.log(response)
            }).catch(e => {console.log(e);});
        },
        clean() {
            this.isShow = false;
            GUIService.getClean().catch(e => {console.log(e); });
        },
        runModel(){
            console.log("selectDataSet: " + this.selectDataSet)
            console.log("selectQuery: " + this.selectQuery)
            console.log("selectQueryNum: " + this.selectQueryNum)
            
            if ( this.selectDataSet && this.selectQuery && this.selectQueryNum) {
                http.get(`/runModel/${this.selectDataSet}-${this.selectQuery}-${this.selectQueryNum}`)
                .then(response => {
                    console.log(response.data);
                    this.displayData = response.data; // json data 
                    this.isShow = true;
                }).catch(e => { console.log(e); })

                this.modelVisible = false;
                console.log("running model")
            } else {
                this.modelVisible = false;
                window.alert("Please select all parameters");
            }
        },
        // onChange methods of selecting params
        changeModel(value) {
            this.selectModel = value;
            console.log("selectModel: "+this.selectModel);
        },
        changeDataSet(value){
            this.selectDataSet = value[0];
            if (this.selectDataSet === "IMDB") {
                    console.log("using imdb")
            }   
            else if(this.selectDataSet === "TPCDS") {
                console.log("using tbcds")
            }
        },
        changeQuery(value){ this.selectQuery = value; },
        changeQueryNum(value) { this.selectQueryNum = value; },

        confirm() { this.modelVisible = true },
        headerChange: (pagination, filters, sorter) => {
            this.displayData = this.displayData.sort()
            console.log("params: " , pagination, filters, sorter); 
        },
        viewTutorial() {
            if (this.showTutorial){
                this.showTutorial = false; 
            } else {
                this.showTutorial = true;
            }
        },
        // generation image 
        generateImage(){
            const planningtime = this.displayData.map(data => data.Planning);
            const nngpPlanning = this.displayData.map(data => data.NNGP_Planning);
            const execution = this.displayData.map(data => data.Execution);
            const nngpExecut = this.displayData.map(data => data.NNGP_Execution);
            axios.post(
                'api/image',
                {   
                    plan: planningtime,
                    execution: execution,
                    nngp_plan: nngpPlanning,
                    nngp_execution: nngpExecut
                },
            ).then((response) => {
                let datasoruce = response.data;

                // problem on updating image
                setTimeout(() => {
                    this.isImageShow = false;
                    this.var_avgImg = "";
                    this.var_execImg = "";
                    this.var_planImg = "";

                    this.var_exec_high = "";
                    this.var_exec_low = "";
                    this.var_plan_high = "";
                    this.var_plan_low = "";

                }, 100);
             
                setTimeout(() => {
                    this.var_avgImg = datasoruce.average;
                    this.var_execImg = datasoruce.execution;
                    this.var_planImg = datasoruce.planning;
                    
                    this.var_exec_high = datasoruce.high_exec;
                    this.var_exec_low = datasoruce.low_exec;
                    this.var_plan_high = datasoruce.high_exec;
                    this.var_plan_low = datasoruce.low_plan;
                    this.isImageShow = true;
                }, 500)
                
                console.log(response)       
            }).catch(error => { console.log(error) })
        },
        switchMode(){
            if (this.isTrain) {
                this.isTrain = false;
            } else {
                this.isTrain = true;
            }
        },
        ratioSelection(){
            let train_percent = (this.train_ratio / 100)  ;
            let test_percent = (100 - this.train_ratio) / 100;

            let join_percent = (this.join_ratio / 100);
            let non_join_percent = (100 - this.join_ratio) / 100;

            let min = this.min_join;
            let max = this.max_join;

            if ( min === "" ) {
                min = 1;
            }
            if ( max === "") {
                max  = 5;
            }

            console.log("train_percent: " + train_percent);
            console.log("test_percent: " + test_percent);

            console.log("join_percent: " + join_percent);
            console.log("non_join_percent: " + non_join_percent);

            if ( this.selectDataSet ) {
                // this.selectDataSet && this.selectQuery && this.selectQueryNum
            } else {
                window.alert("Please select all parameters");
            }
            axios.post(
                'api/ratio',
                {   
                    relation_name: [this.selectDataSet],
                    train_test_ratio: {
                        "train": [ train_percent ],
                        "test" : [ test_percent ]
                    },
                    non_join_query_ratio : {
                        "join": [ join_percent ],
                        "non_join" : [ non_join_percent ]
                    },
                    min_max : {
                        "min": [ min ],
                        "max": [ max ]
                    }
                },
            ).then((response) => {
                console.log(response)
            }).catch(error => { console.log(error) })
        },
    }
}
</script>

